import React, { Component } from "react";
import { ReactDOM } from "react";
import { Button, Modal } from "react-bootstrap";
import _, { times } from "lodash";
import Api from "../../classes/Api";

class CustomerFormModal extends Component {
	constructor(props) {
		super(props);
		this.state = {
			errors: {},
			selectedShipmentMethod: this.props.contaktiChat.shipment_methods[0],
			selectedCheckoutMethod: this.props.contaktiChat.checkout_methods[0],
			disabledFormContent: false,
		};
		this.customerFormInputs = [
			"full_name",
			"email",
			"phone_number",
			"street_address",
			"city",
			"zip_code",
		];
	}

	componentDidMount() {
		document.addEventListener("keydown", (e) => {
			if (e.key === "Escape") {
				e.preventDefault();
			}
		});
	}

	handleClose = () => {
		this.setState({ errors: {} });
		this.props.handleClose();
	};

	submitForm = () => {
		this.form.dispatchEvent(
			new Event("submit", { cancelable: true, bubbles: true })
		);
	};
	errors(key) {
		if (this.state.errors[key]) {
			return (
				<span
					className="w-100 text-end text-danger"
					style={{ fontSize: "0.75rem" }}
				>
					{`${this.props.translations.user_dashboard.shopping_cart.customer[key]} `}
					{this.state.errors[key].map((e) => {
						return e;
					})}
				</span>
			);
		}
	}

	setPluginDisabledState = (disabled) => {
		this.setState({ disabledFormContent: disabled });
		this.props.setPluginDisabledState(disabled);
	};

	handleShipmentChange = (event) => {
		let selectedId = event.target.value;
		let selected_method = this.state.selectedShipmentMethod;
		_.each(this.props.contaktiChat.shipment_methods, (method) => {
			if (selectedId == method.id) {
				selected_method = method;
			}
		});
		this.setState({ selectedShipmentMethod: selected_method });
	};

	handleCheckoutMethodChange = (event) => {
		this.setState({ selectedCheckoutMethod: event.target.value });
	};

	handleSubmit = async (e) => {
		this.setState({
			errors: {},
		});
		e.preventDefault();
		this.setPluginDisabledState(true);
		let customerData = {};
		let form = e.target;
		customerData[ "customer" ] = {};
		customerData["customer"]["full_name"] = form.full_name.value;
		customerData["customer"]["email"] = form.email.value;
		customerData["customer"]["phone_number"] = form.phone_number.value;
		customerData["customer"]["street_address"] = form.street_address.value;
		customerData["customer"]["city"] = form.city.value;
		customerData["customer"]["zip_code"] = form.zip_code.value;

		const url = "customers";
		let createdCustomer = await new Api().post(url, customerData);
		if (createdCustomer && !createdCustomer.errors) {
			this.props.handleCartOrder(
				createdCustomer.id,
				this.state.selectedCheckoutMethod,
				this.state.selectedShipmentMethod
			);
		} else if (createdCustomer.errors) {
			this.setState({
				errors: createdCustomer.errors,
			});
			this.setPluginDisabledState(false);
		} else {
			this.setPluginDisabledState(false);
		}
	};

	render() {
		const cartTranslations =
			this.props.translations.user_dashboard.shopping_cart;
		const customerTranslations = cartTranslations.customer;

		return (
			<>
				<Modal
					show={this.props.show}
					onHide={this.handleClose}
					backdrop="static"
				>
					<div
						className={
							this.state.disabledFormContent == true ? "disabled-content" : ""
						}
					>
						<Modal.Header closeButton className="py-1">
							<Modal.Title>{customerTranslations.title}</Modal.Title>
						</Modal.Header>
						<Modal.Body>
							<form
								ref={(f) => {
									this.form = f;
								}}
								onSubmit={(e) => {
									this.handleSubmit(e);
								}}
								className="form-group mb-1"
							>
								{this.customerFormInputs.map((field_name) => {
									return (
										<>
											<div className="d-flex w-100 justify-content-between">
												<span
													className="fw-bold"
													style={{ fontSize: "0.8rem" }}
												>
													{customerTranslations[field_name]}
												</span>
												<span>{this.errors(field_name)}</span>
											</div>
											<input
												type="text"
												required
												className="form-control py-1"
												style={{ fontSize: "0.7rem" }}
												name={field_name}
											/>
										</>
									);
								})}
							</form>

							<div className="row form-group">
								<div className="col-6">
									<label className="fw-bold" style={{ fontSize: "0.8rem" }}>
										{cartTranslations.checkout_method}
									</label>
									<div className="centera" id="checkout_method_select">
										<select
											className="form-control w-100 py-1"
											style={{ fontSize: "0.7rem" }}
											onChange={this.handleCheckoutMethodChange}
											value={this.state.selectedCheckoutMethod}
										>
											{this.props.contaktiChat.checkout_methods.map(
												(method) => {
													return (
														<option value={method}>
															{this.props.translations.service_channels[method]}
														</option>
													);
												}
											)}
										</select>
									</div>
								</div>

								<div className="col-6">
									<label className="fw-bold" style={{ fontSize: "0.8rem" }}>
										{this.props.translations.shipment_methods.title}
									</label>
									<div className="centera" id="shipment_method_select">
										<select
											className="form-control w-100 py-1"
											style={{ fontSize: "0.7rem" }}
											onChange={this.handleShipmentChange}
											value={this.state.selectedShipmentMethod.id}
										>
											{this.props.contaktiChat.shipment_methods.map(
												(method, index) => {
													return (
														<option value={method.id} key={index}>
															{method.name}
														</option>
													);
												}
											)}
										</select>
									</div>
								</div>
							</div>
						</Modal.Body>
						<Modal.Footer className="py-1">
							<Button
								variant="secondary"
								style={{ background: this.props.buttonColor }}
								size="sm"
								onClick={this.submitForm}
							>
								{this.state.selectedCheckoutMethod == "checkout_paytrail"
									? cartTranslations.checkout_button_paytrail
									: cartTranslations.checkout_button_ticket}
							</Button>
						</Modal.Footer>
					</div>
				</Modal>
			</>
		);
	}
}

export default CustomerFormModal;
